//====================================================================================================
.header { 
	position: absolute;
	top: 0px;
	margin-top: 15px;
	width: 100%;
	z-index: 40;
	height: 57px;
	box-shadow: $shadow;
	background: $second;
	animation: show-in 0s ease-in-out 0.05s alternate forwards;
	transform: translateY(-100px);
	transition: top 0.3s;
	@media (max-width: $md3+px) {
		margin-top: 0px;
		height: 45px;
	}
	&_fixed {
		position: fixed;
		z-index: 9999;
		top: 0;
		left: 0;
		right: 0;
		margin-top: 0px;
		height: 48px;
		@media (max-width: $md3+px) {
			margin-top: 0px;
			height: 45px;
		}
	}
	&__wrap {
		@include flexbox();
		align-items: center;
		height: 100%;
	}
	&__logo {
		position: relative;
		z-index: 3;
	}
	.container {
		height: 100%;
	}
}

.logo {
	display: block;
	&__img {
		display: block;
		width: 60px;
		margin-right: 10px;
	}
}

.language {
	position: relative;
	font-size: 14px;
	margin-right: 0;
	margin-left: auto;
	height: 23px;
	line-height: 23px;
	border-radius: 10px;
	box-shadow: $shadow;
	padding-left: 28px;
	z-index: 3;
	cursor: pointer;
	background: #ffffff;
	box-shadow: inset 5px -2px 7px #ededed;
	&:hover {
		.language__list {
			height: auto;
			opacity: 1;
		}
	}
	&__default {
		text-align: center;
		display: block;
		position: relative;
		background: lighten($accent, 5%);
		color: $second;
		padding: 0px 10px;
		font-weight: 600;
		border-radius: 15px;
		height: 23px;
		line-height: 23px;
		min-width: 55px;
	}
	&__arrow {
		display: block;
		position: absolute;
		height: 15px;
		width: 15px;
		top: 50%;
		left: 6px;
		transform: translateY(-50%);
		opacity: 1;
		border-radius: 0px 0px 2px 2px;
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__list {
		position: absolute;
		top: 100%;
		left: 7px;
		right: 7px;
		background: $second;
		box-shadow: $shadowligth ;
		height: 0px;
		overflow: hidden;
		opacity: 0;
		transition: opacity 0.3s;
	}
	&__link {
		border-bottom: 1px solid $first;
		display: block;
		color: $accent;
		font-weight: 600;
		text-transform: uppercase;
		padding: 0px 2px;
		text-align: center;
		transition: all 0.2s;
		&:hover {
			background: $first;
		}
		&:active {
			background: $accent;
			border-bottom: 1px solid $accent;
			color: $second;
		}    
	}
}

@keyframes show-in {
	0%{
		transform: translateY(-100px);
		opacity: 0.7;
}

	100%{
		transform: translateY(0px);
		opacity: 1;
}
}

.menu {
	font-size: 14px;
	margin-left: auto;
	margin-right: 0px;
	height: 40px;
	@media (max-width: $md3+px) {
		height: auto;
	}
	&__icon {
		display: none;
		position: relative;
		width:30px;
		height: 18px;
		align-items: center;
		z-index: 999;
		cursor: pointer;
		span{
			@include anim(0.3s,0s);top: 8px;left: 0px;position: absolute;width: 100%;height: 2px; background: $accent;  
			&:first-child{top: 0px;}
			&:last-child{top: auto;bottom: 0px;}
		}
		&.active{
			span{@include scale(0);
				&:first-child{@include rotate(-45deg);top: 8px;}
				&:last-child{@include rotate(45deg);bottom: 8px;}
			}
		}
		@media (max-width: $md3+px) {
			display: block;
		}
	}

	&__container {
		@media (max-width: $md3+px) {
			display: block; 
			position: fixed;
			height: 100vh;
			width: 100%;
			top: 0px;
			left: -100%;
			background-color: $second;
			z-index: 2;
			padding: 150px 0px 0px 0px;
			border-right: 1px solid rgba($accent, 0.2); 
			overflow: auto;
			border-right: 7px solid $accent;
			@include anim(0.4s,0s);
			&.active { 
				left: 0;
			}
		}
	}

	&__list {
		@include flexbox();
		border-radius: 20px;
		background: #ffffff;
		box-shadow: inset -5px -5px 20px #ebebeb;
		overflow: hidden;
		@media (max-width: $md3+px) {
			display: block;
			box-shadow: none;
			border-radius: 0px;
			border-top: 1px solid $first;
		}
	}
	&__link {
		padding: 0px;
		display: block;
		font-size: 14px;
		height: 40px;
		line-height: 40px;
		color: $accent;
		padding: 0px 30px;
		text-transform: uppercase;
		font-weight: 600;
		transition: all 0.3s;
		&:hover {
			background: lighten($accent, 1%);
			color: $second;
			@media (max-width: $md3+px) {
				border-bottom: none;
			}
		}
		&:active {
			background: darken($accent, 1%);
			@media (max-width: $md3+px) {
				border-bottom: none;
			}
		}
		@media (max-width: $md3+px) {
			border-bottom: 1px solid $first;
		}
	}
	.current {
		background: $accent;
		color: $second;
		border-right: 0px solid $accent;
		@media (max-width: $md3+px) {
			border-bottom: none;
		}
	}
}
