.skills { 
	background: $first;
	padding: 90px 0px;
	@media (max-width: $md2+px) {
		padding: 36px 0px;
	}
	&__wrap {
		position: relative;
		padding-right: 430px;
		@media (max-width: $md2+px) {
			padding-right: 0px;
		}
	}
	&__container {
		height: 100%;
		margin: 0px -10px;
		& > * {
			max-width: 100%;
			padding: 0px 10px;
			margin-bottom: 20px;
			align-items: stretch;
		}
		
	}
	&__bg {
		position: absolute;
		width: 400px;
		top: -270px;
		right: 0px;
		@media (max-width: $md2+px) {
			display: none;
		}
	}
}

.skills-block {
	padding-left: 15px;
	border-radius: 15px;
	padding: 20px;
	box-shadow: $shadow;
	background: $second;
	height: 100%;
	@media (max-width: $md3+px) {
		padding: 15px;
	}
	&__title {  
		color: $accent;
		font-size: 20px;
		font-weight: 900;
		margin-bottom: 25px;
	}
	&__list {
		padding-left: 20px;
		@media (max-width: $md2+px) {
			padding-left: 10px;
		}
	}
	&__list-item {
		position: relative;
		font-size: 16px;
		line-height: 1.6;
		font-weight: 600;
		padding-left: 20px;
		margin-bottom: 10px;
		& > * {
			margin-right: 10px;
		}
		&:before {
			content: '';
			position: absolute;
			height: 7px;
			width: 7px;
			border-radius: 50%;
			background: $accent;
			top: 10px;
			left: 0px;
		}
	}
}
.laptop-preview {
	&__img {
		max-width: 100%;
	}
	&__text {
		position: absolute;
		top: 90px;
		left: 150px;
		height: 100px;
		width: 150px;
		transform:  rotateY(-36deg) rotateZ(14deg) translateX(-2px) skewX(-3deg) skewY(12deg);
		span {
			line-height: 1.6;
			white-space: pre;
			font-weight: 600;
			font-size: 16px;
			color: $second;
		}
	}
}

.skill {
	display: inline-block;
	font-size: 14px;
	color: $second;
	margin-bottom: 4px;
	padding: 2px 15px;
	border-radius: 10px;
	background: $accent;
	box-shadow: $shadow;
	&_accent {
		background: $accent2;
	}
}