.contacts { 
	position: relative;
	z-index: 1;
	display: block;
	background: $second;
	padding: 90px 0px;
	@media (max-width: $md3+px) {
		padding: 36px 0px;
	}
	&__wrap {
		position: relative;
	}
	.active {
		display: block;
	}
	.unactive {
		display: none;
	}
	.contacts-block {
		position: absolute;
		top: 0px;
		right: 0px;
		@media (max-width: $md3+px) {
			position: static;
			margin-bottom: 35px;
			padding-left: 10px;
		}
	}
}

.contact-form {
	&__form {
		display: block;
	}
	&__item {
		margin-bottom: 20px;
		width: 100%;
		&_flex {
			@include flexbox();
			align-items: center;
		}
	}
	&__notification {
		margin-right: 25px;
		white-space: pre-wrap;
		margin-left: auto;
		line-height: 1.6;
		margin-bottom: 4px;
		@media (max-width: $md4+px) {
			margin-right: 15px;
		}
	}
	&__input {
		width: 100%;
		max-width: 100%;
		padding: 8px 17px;
		border: none;
		border: 1px solid $first;
		border-radius: 15px ;
		margin: 0;
		box-shadow: $shadow;
		font-size: 16px;
		resize: none;
		max-width: 400px;
		display: block;
		margin-bottom: 5px;
		@media (max-width: $md4+px) {
			max-width: 100%;
		}
	}
	&__errors {
		font-size: 15px;
		display: block;
		margin-top: 5px;
		padding-left: 10px;
		color: rgba(223, 27, 27, 0.884);
	}
	&__button {
		display: block;
		margin-left: 0px;
	}
	&__message {
		max-width: 100%;
	}
	.error {
		border: 1px solid rgba(245, 217, 217, 0.884);
	}
}

.form-status {
	width: 100%;
	background: $first;
	border-radius: 15px;
	display: none;
	&__wrap {
		@include flexbox();
		position: relative;
		align-items: center;
		justify-content: center;
		min-height: 200px;
	}
	&__container {
		text-align: center;
		padding: 20px;
	}
	&__message {
		font-size: 16px;
		margin-bottom: 20px;
	}
	&__button {
		display: inline-block;
		margin: 0 auto;
	}
}