.portfolio {
  background: $first;
  padding: 90px 0px;
  padding-top: 0px;

  @media (max-width: $md3+px) {
    padding: 40px 0px;
    padding-top: 0px;
  }

  &__wrap {
    position: relative;
    height: auto;
    display: flex;
  }

  &__colection {
    position: relative;
  }

  &__list {
    @include flexbox();
    flex-wrap: wrap;
    margin: 0px -15px;
    margin-bottom: -20px;

    @media (max-width: $md1+px) {
      margin: 0px -7.5px;
      margin-bottom: -15px;
    }

    @media (max-width: $md4+px) {
      display: block;
    }

    &>* {
      flex: 1 1 50%;
      max-width: 50%;
      padding: 0px 15px;
      align-items: stretch;
      margin-bottom: 30px;

      @media (max-width: $md1+px) {
        padding: 0px 7.5px;
      }

      @media (max-width: $md2+px) {
        flex: 1 1 33.33%;
        max-width: 33.33%;
      }

      @media (max-width: $md3+px) {
        flex: 1 1 50%;
        max-width: 50%;
      }

      @media (max-width: $md4+px) {
        max-width: 100%;
      }
    }
  }

  &__robot {
    position: sticky;
    min-width: 450px;
    top: 0px;
    height: 100vh;

    @media (max-width: $md2+px) {
      min-width: 350px;
    }

    @media (max-width: $md2+px) {
      display: none;
    }
  }

  &__subtitle {
    font-size: 18px;
  }

  .bg {
    position: absolute;
    top: -300px;
    right: -50px;

    &__square {
      width: 70px;
      transform: rotate(-50deg);
    }
  }
}

.portfolio-item {
  &__info {
    padding-bottom: 10px;
  }

  &__link {
    display: block;
    position: relative;
    font-size: 14px;
    background: $second;
    border-radius: 2px;
    box-shadow: $shadowligth;
    padding: 20px;
    height: 100%;
    color: #000000;
    position: relative;
    overflow: hidden;
    transition: box-shadow 0.2s;

    &:hover {
      box-shadow: $shadow;

      .portfolio-item__cover {

        &:before,
        &:after {
          opacity: 1;
        }

        &:after {
          &:hover {
            background: darken($accent, 15%);
          }
        }
      }
    }
  }

  &__cover {
    position: relative;
    padding-top: 90%;
    margin: 0px -20px;
    margin-bottom: 15px;
    margin-top: -20px;
    overflow: hidden;
    background: $first;

    &:before {
      content: '';
      position: absolute;
      top: 0px;
      left: 0px;
      bottom: 0px;
      right: 0px;
      background: rgba(0, 0, 0, 0.2);
      z-index: 1;
      opacity: 0.1;
      transition: opacity 0.3s;
    }

    &:after {
      content: 'view project';
      position: absolute;
      top: 50%;
      left: 50%;
      height: 40px;
      background: $accent;
      line-height: 40px;
      padding: 0px 25px;
      border-radius: 20px;
      color: $second;
      font-weight: 600;
      text-transform: uppercase;
      font-size: 13px;
      z-index: 2;
      opacity: 1;
      transform: translateX(-50%) translateY(-50%);
      transition: opacity 0.3s;
      opacity: 0;

      @media (max-width: $md1+px) {
        display: none;
      }
    }

    img {
      background: $first;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      border-radius: 2px;
      box-shadow: $shadowligth;
      width: 100%;
      overflow: hidden;
      transition: transform 0.3s;

      &:before {
        content: "";
        position: absolute;
        top: 0px;
        left: 0px;
        bottom: 0px;
        right: 0px;
        background: $first;
        z-index: 0;
      }

      &:after {
        content: "Sorry, image link crashed :(";
        display: block;
        width: 50%;
        text-align: center;
        font-size: 12px;
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
      }
    }
  }

  &__title {
    font-weight: 600;
    margin-bottom: 10px;
    line-height: 1.4;
    font-size: 16px;
    max-height: 40px;
    overflow: hidden;
    text-transform: uppercase;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
  }

  &__description {
    line-height: 1.5;
    font-size: 13px;
    height: 125px;
    font-size: 14px;
    margin-bottom: 25px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 6;
  }

  &__subtitle {
    position: absolute;
    bottom: 0px;
    right: 20px;
    left: 20px;
    line-height: 1.8;
    background: $accent;
    color: $second;
    padding: 3px 20px;
    font-size: 13px;
    text-transform: uppercase;
    margin: 0px -20px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
  }

  .accent {
    background: $accent2;
  }
}

.robot {
  position: absolute;
  top: 35%;
  right: 130px;

  &:before {
    content: 'Click me';
    position: absolute;
    display: block;
    height: 70px;
    padding: 15px;
    width: 70px;
    letter-spacing: 1px;
    line-height: 1.1;
    display: flex;
    align-items: center;
    box-shadow: $shadow;
    font-size: 14px;
    font-weight: 600;
    color: $first;
    text-align: center;
    border-radius: 50%;
    background: $accent;
    top: 270px;
    right: -40px;
    transform: rotateZ(-30deg);
  }

  &__init {
    cursor: pointer;
  }

  &__text {
    position: absolute;
    top: 40px;
    right: 75px;
    z-index: 1;
    color: $second;
    font-size: 14px;
    animation: robot_bounce2 1.1s ease-in-out 0.05s infinite alternate;
    width: 135px;
    @include flexbox();
    justify-content: center;

    span {
      text-align: center;
      font-size: 16px;
      line-height: 1.4;
    }
  }

  ;
}

.robot svg {
  position: sticky;
  top: 0;
  display: block;
  margin: 0 auto;
  transform: scale(0.7) translateX(38%) translateY(-55%);
  height: 800px;
  width: 600px;
}

@keyframes robot_bounce2 {

  0% {
    transform: translateY(80px)
  }


  100% {
    transform: translateY(45px)
  }
}

@keyframes robot_bounce {

  0% {
    transform: translateY(80px)
  }

  100% {
    transform: translateY(30px)
  }
}

@keyframes shadow {

  0% {
    transform: scale(1.5, 1.2);
    opacity: 0.3;
    box-shadow: $shadow;
  }

  100% {
    transform: scale(1, 1);
    opacity: 0.2
  }
}

@keyframes arms_bounce_left {

  0% {
    transform: rotate(0deg)
  }

  100% {
    transform: rotate(10deg)
  }
}

@keyframes arms_bounce_right {

  0% {
    transform: rotate(0deg)
  }

  100% {
    transform: rotate(10deg)
  }
}

@keyframes eyes_blink {

  0% {
    transform: scale(1, 1)
  }

  90% {
    transform: scale(1, 1)
  }

  95% {
    transform: scale(0.8, 0)
  }

  100% {
    transform: scale(1, 1)
  }
}

.robot__body {
  animation: robot_bounce 1.1s ease-in-out 0s infinite alternate
}

.robot__head {
  animation: robot_bounce 1.1s ease-in-out 0.05s infinite alternate
}

.robot__arms {
  animation: robot_bounce 1.1s ease-in-out 0.1s infinite alternate
}

.robot__arms .robot__left {
  transform-origin: center right;
  animation: arms_bounce_left 1.1s ease-in-out 0s infinite alternate
}

.robot__arms .robot__right {
  transform-origin: center left;
  animation: arms_bounce_right 1.1s ease-in-out 0s infinite alternate
}

@keyframes arm_greeting_right {

  0% {
    transform: rotate(-10deg) scaleY(-1)
  }

  100% {
    transform: rotate(5deg) scaleY(-1)
  }
}

.robot__init {

  &:hover,
  &_onstart {
    .robot__arms .robot__right {
      transform-origin: center left;
      animation: arm_greeting_right 0.5s ease-in-out 0s 5 alternate
    }
  }
}

.robot__eyes ellipse {
  transform-origin: center center;
  animation: eyes_blink 2s ease-out 0s infinite alternate
}

.robot__hover ellipse {
  transform-origin: center center;
  animation: shadow 1.1s ease-in-out 0s infinite alternate
}