
.main {
	position: relative;
	height: 100vh;
	// max-height: 800px;
	width: 100%;
	padding-top: 72px;
	overflow: hidden; 
	@media (max-width: $md3+px) {
		height: auto;
		max-height: none;
		padding-bottom: 40px;
	}
	.container {
		height: 100%;
		@media (max-width: $md3+px) {
			height: auto;
		}
	}
	&__wrap	{
		position: relative; 
		@include flexbox();
		height: 100%;
		@media (max-width: $md3+px) {
			display: block;
			height: auto;
		}
	}
	&__photo {
		position: relative;
		flex: 0 1 32%;
		@include flexbox();
		flex-direction: column;
		justify-content: center;
		@media (max-width: $md2+px) {
			flex: 0 1 40%;
		}
		@media (max-width: $md3+px) {
			width: 400px;
			margin: 0 auto;
		}
		@media (max-width: $md4+px) {
			width: 250px;
		}
	}
	&__info {
		flex: 1;
		@include flexbox();
		align-items: center;
	}
	.contacts-block {
		position: absolute;
		bottom: 80px;
		left: 50%;
		transform: translateX(-59%);
		@media (max-width: $md3+px) {
			position: static;
			transform: none;
			margin: 0 auto;
			margin-bottom: 45px;
		}
	}
}

.user-photo {
	position: relative;
	width: 100%;
	padding-top: 100%;
	&__img {
		position: absolute;
		top: 0px;
		width: 100%;
		height: 100%;
		object-fit: contain;
		@media (max-width: $md3+px) {
			transform: translateX(6%);
		}
	}
}

.content {
	font-size: 14px;
	&__first {
		text-transform: uppercase;
		font-size: 20px;
		color: $textLight;
		margin-bottom: 10px;
		@media (max-width: $md3+px) {
			text-align: center;
			margin-bottom: 20px;
		}
	}
	&__title {
		font-size: 35px;
		text-transform: uppercase;
		font-weight: 900;
		margin-bottom: 15px;
		span {
			color: $accent;
		}
		@media (max-width: $md3+px) {
			text-align: center;
			margin-bottom: 25px;
		}
		@media (max-width: $md4+px) {
			font-size: 25px;
		}
	}
	&__description {
		line-height: 1.6;
		font-size: 16px;
		margin-bottom: 20px;
		span {
			color: #000000;
			font-weight: 600;
			font-size: 18px;
		}
		@media (max-width: $md3+px) {
			text-align: center;
			margin-bottom: 25px;
		}
	}
	&__button {
		@media (max-width: $md3+px) {
			text-align: center;
		}
	}
}

.bg {
	position: absolute;
	top: 50px;
	right: -90px;
	&__square {
		width: 70px;
		transform: rotate(-50deg);
	}
}