.review {
  padding-top: 85px;
  padding-bottom: 65px;

  .container {
    max-width: 1000px;
  }

  &__title {
    margin-bottom: 35px;

    &_small {
      font-size: 26px;
      margin: 0;
      margin-bottom: 20px;
      border-bottom: 1px solid $first;
      padding-bottom: 5px;
    }
  }

  &__link {
    margin-bottom: 35px;
    padding-left: 25px;

    a {
      margin-right: 15px;
    }
  }

  &__description {
    margin-bottom: 35px;
    font-size: 16px;
    color: $accent;
    line-height: 1.5;
  }

  &__cover {
    width: 100%;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: $shadow;
    margin-bottom: 35px;

    img {
      max-width: 100%;
      width: 100%;
    }
  }

  &__video {
    width: 100%;
    max-width: 950px;

    div {
      display: inline-block;
      margin-bottom: 35px;
      position: relative;
      width: 100%;
      padding-top: 51.7%;
    }

    iframe {
      position: absolute;
      top: 0px;
      left: 0px;
      height: 100%;
      width: 100%;
      border-radius: 15px;
      box-shadow: $shadow;
    }
  }

  &__section {
    padding-left: 25px;
    margin-bottom: 55px;

    @media (max-width: $md2+px) {
      padding-left: 5px;
    }
  }

  &__section-item {
    position: relative;
    padding-left: 20px;
    margin-bottom: 25px;

    @media (max-width: $md2+px) {
      padding-left: 15px;
    }

    &:before {
      content: '';
      position: absolute;
      height: 7px;
      width: 7px;
      border-radius: 50%;
      background: $accent;
      top: 13px;
      left: 0px;
    }
  }

  &__list {
    padding-left: 30px;
    margin-bottom: 20px;

    @media (max-width: $md2+px) {
      padding-left: 15px;
    }

    &-item {
      position: relative;
      padding-left: 20px;
      font-size: 16px;
      line-height: 1.5;
      margin-bottom: 5px;

      @media (max-width: $md2+px) {
        padding-left: 15px;
      }

      &:before {
        content: '';
        position: absolute;
        height: 7px;
        width: 7px;
        border-radius: 50%;
        background: $accent2;
        top: 10px;
        left: 0px;
      }
    }
  }

  &__subtitle {
    font-size: 21px;
    font-weight: 600;
    color: $accent;
    text-transform: uppercase;
    line-height: 30px;
    margin-bottom: 15px;
  }

  &__paragraph {
    font-size: 16px;
    font-weight: 500;
    padding-left: 25px;
    line-height: 1.5;
    margin-bottom: 15px;

    @media (max-width: $md2+px) {
      padding-left: 15px;
    }

    span {
      font-weight: 600;
      color: $accent;
    }

    &_dots {
      position: relative;

      &:before {
        content: '';
        position: absolute;
        height: 7px;
        width: 7px;
        border-radius: 50%;
        background: $accent;
        top: 9px;
        left: 0px;
      }
    }
  }

  .accent {
    color: $accent;
    font-weight: 600;
  }

  .link {
    text-decoration: underline;
  }

  .preview {
    margin-bottom: 45px;

    &_large {
      .preview__list {
        display: block;

        &>* {
          max-width: 100%;
        }
      }
    }

    &__list {
      @include flexbox();
      flex-wrap: wrap;
      margin: 0px -10px;
      margin-bottom: -20px;

      &>* {
        flex: 1 1 33.33%;
        max-width: 33.33%;
        padding: 0px 10px;
        align-items: stretch;
        margin-bottom: 20px;

        @media (max-width: $md2+px) {
          flex: 1 1 33.33%;
          max-width: 33.33%;
        }

        @media (max-width: $md3+px) {
          flex: 1 1 50%;
          max-width: 50%;
        }

        @media (max-width: $md4+px) {
          max-width: 100%;
        }
      }
    }

    &__link {
      position: relative;
      display: block;
      height: 100%;
      max-height: 600px;
      overflow: hidden;
      box-shadow: $shadowligth;

      &:before {
        content: '';
        position: absolute;
        top: 0px;
        left: 0px;
        bottom: 0px;
        right: 0px;
        background: rgba(0, 0, 0, 0.2);
        z-index: 1;
        opacity: 0.1;
        transition: opacity 0.3s;
      }

      &:after {
        content: 'view';
        position: absolute;
        top: 50%;
        left: 50%;
        height: 40px;
        background: $accent;
        line-height: 40px;
        padding: 0px 25px;
        border-radius: 20px;
        color: $second;
        font-weight: 600;
        text-transform: uppercase;
        font-size: 13px;
        z-index: 2;
        opacity: 1;
        transform: translateX(-50%) translateY(-50%);
        transition: opacity 0.3s;
        opacity: 0;

        @media (max-width: $md1+px) {
          display: none;
        }
      }

      &:hover {

        &:before,
        &:after {
          opacity: 1;
        }
      }

      &_unactive {
        height: initial;
      }

      &_unactive:hover {

        &:before,
        &:after {
          display: none;
        }
      }
    }

    img {
      width: 100%;
      max-width: 100%;
    }
  }
}