.contacts-block {
	@include flexbox();
	align-items: flex-end;
	&__item {
		margin-right: 10px;
		&:nth-child(1) {
			.contacts-block__img {
				width: 35px;
				border-radius: 50%;
			}
		}
	&:last-child {
		margin-right: 0px;
	}
	}
	&__link {
		position: relative;
		display: block;
		height: 30px;
		min-width: 30px;
		border-radius: 50%;
		transition: background 0.2s;
		font-size: 14px;
		&_descr {
			padding-left: 30px;
			padding-right: 15px;
			border-radius: 15px;
			line-height: 30px;
			.contacts-block__img {
				left: 16px;
			}
		}
		&:hover {
			background: $accent;
			box-shadow: $shadow;
			.contacts-block__img {
				fill: $second;
			}
			.contacts-block__description {
				color: $second;
			}
		}
	}
	&__description {
		color: #000000;
		font-size: 12px;
		font-weight: 600;
		transition: color 0.2s;
	}
	&__img {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		width: 15px;
		box-shadow: $shadow;
		transition: fill 0.2s;
		&:hover {
			fill: $accent;
		}
	}
}