
@import "fonts.scss";
@import "mixins.scss";
@import "vars.scss";
@import "normalize.scss";

.title {
	font-size: 35px;
	text-transform: uppercase;
	font-weight: 900;
	margin-bottom: 35px;
	color: $accent;
	@media (max-width: $md3+px) {
		font-size: 30px;
		margin-bottom: 25px;
		text-align: center;
	}
	&_small {
		font-size: 20px;
		margin-bottom: 25px;
		margin-left: 20px;
	}
}

.button {
  position: relative;
  text-align: center;
  display: inline-block;
  margin-right: 0px;
  margin-left: auto;
  background: lighten($accent, 5%);
  text-transform: uppercase;
  color: $second;
  height: 40px;
  line-height: 40px;
  padding: 0px 30px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  box-shadow: $shadow;
  transition: background 0.2s;
  &__icon {
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    width: 12px;
    height: 12px;
    object-fit: contain;
  }
  &:hover {
    background: lighten($accent, 10%);
  }
  &:active {
    background: darken($accent, 10%);
	}
	&:disabled {
		background: lighten($accent, 30%);
	}
}

