.breadcrumbs {
	display: block;
	height: 60px;
	overflow: auto;
	padding: 0px 10px 10px 0px;
	@include flexbox();
	justify-content: flex-start;
	align-items: center;
	@media (max-width: $md3+px) {
		display: block;
	}
	&__link {
		display: inline-block;
		font-size: 14px;
		text-align: center;
		border-radius: 14px;
		max-width: 200px;
		white-space: nowrap;
		color: lighten($accent, 12%);
		@media (max-width: $md3+px) {
			margin-bottom: 7px;
		}
	}
	&__arrow {
		display: inline-block;
		padding: 0px 5px;
		color: $accent; 
		font-size: 14px;
	}
	&__current {
		display: inline-block;
		white-space: nowrap;
		font-size: 14px;
		@media (max-width: $md3+px) {
			white-space: normal;
		}
	}
}